/* ============================================
   OUTSTANDING PORTFOLIO - WORLD CLASS
   Monochrome Brand Colors: #050a1f (Dark Navy) & #b4e8c9 (Mint Green)
   Palette: https://huemint.com/brand-1/#palette=b4e8c9-050a1f
   ============================================ */

/* Fonts are loaded asynchronously in HTML to prevent render blocking */

/* ============================================
   PERFORMANCE OPTIMIZATIONS
   ============================================ */
/* GPU acceleration for animations - prevents forced reflows */
.hero-image,
.case-study-thumb,
.testimonial-card,
.service-item-minimal {
  will-change: transform;
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/* ============================================
   COLOR SYSTEM - MONOCHROME
   ============================================ */
:root {
  /* Primary Colors */
  --color-dark: #050a1f;
  --color-light: #b4e8c9;
  
  /* Variations */
  --color-dark-90: rgba(5, 10, 31, 0.9);
  --color-dark-80: rgba(5, 10, 31, 0.8);
  --color-dark-50: rgba(5, 10, 31, 0.5);
  --color-dark-30: rgba(5, 10, 31, 0.3);
  --color-dark-20: rgba(5, 10, 31, 0.2);
  --color-dark-10: rgba(5, 10, 31, 0.1);
  --color-dark-05: rgba(5, 10, 31, 0.05);
  
  --color-light-90: rgba(180, 232, 201, 0.9);
  --color-light-80: rgba(180, 232, 201, 0.8);
  --color-light-50: rgba(180, 232, 201, 0.5);
  --color-light-40: rgba(180, 232, 201, 0.4);
  --color-light-30: rgba(180, 232, 201, 0.3);
  --color-light-20: rgba(180, 232, 201, 0.2);
  --color-light-10: rgba(180, 232, 201, 0.1);
  --color-light-05: rgba(180, 232, 201, 0.05);
  
  /* Backgrounds */
  --bg-primary: #050a1f;
  --bg-secondary: #050a1f;
  --bg-light: #b4e8c9;
  --bg-dark-section: rgba(5, 10, 31, 0.98);
  
  /* Text */
  --text-primary: #b4e8c9;
  --text-secondary: rgba(180, 232, 201, 0.85);
  --text-tertiary: rgba(180, 232, 201, 0.7);
  --text-light: #b4e8c9;
  --text-on-dark: rgba(180, 232, 201, 0.95);
  --text-on-light: #050a1f;
  
  /* Shadows */
  --shadow-xs: 0 1px 2px rgba(5, 10, 31, 0.3);
  --shadow-sm: 0 2px 8px rgba(5, 10, 31, 0.4);
  --shadow-md: 0 4px 16px rgba(5, 10, 31, 0.5);
  --shadow-lg: 0 8px 32px rgba(5, 10, 31, 0.6);
  --shadow-xl: 0 16px 48px rgba(5, 10, 31, 0.7);
  --shadow-light: 0 8px 32px rgba(180, 232, 201, 0.4);
  --shadow-light-lg: 0 16px 48px rgba(180, 232, 201, 0.5);
  
  /* Typography */
  --font-sans: 'Nunito Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  --font-serif: 'Space Grotesk', sans-serif;
  --font-grotesk: 'Space Grotesk', sans-serif;
  
  /* Spacing */
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-12: 3rem;
  --space-16: 4rem;
  --space-20: 5rem;
  --space-24: 6rem;
  --space-32: 8rem;
  
  /* Transitions */
  --ease-out-expo: cubic-bezier(0.16, 1, 0.3, 1);
  --ease-in-out-expo: cubic-bezier(0.87, 0, 0.13, 1);
  --ease-spring: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  
  /* Border Radius */
  --radius-sm: 0.5rem;
  --radius-md: 1rem;
  --radius-lg: 1.5rem;
  --radius-xl: 2rem;
  --radius-full: 9999px;
}

/* ============================================
   RESET
   ============================================ */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ============================================
   ACCESSIBILITY - SKIP TO MAIN CONTENT
   ============================================ */
.skip-to-main-content {
  position: absolute;
  top: -100px;
  left: 0;
  z-index: 10000;
  padding: 1rem 2rem;
  background: var(--color-light);
  color: var(--color-dark);
  text-decoration: none;
  font-weight: 700;
  border-radius: 0 0 0.5rem 0;
  transition: top 0.3s ease;
}

.skip-to-main-content:focus,
.skip-to-main-content:focus-visible {
  top: 0;
  outline: 3px solid var(--color-light);
  outline-offset: 2px;
}

/* Screen reader only class */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* ============================================
   ACCESSIBILITY - FOCUS STYLES
   ============================================ */
/* Global focus styles for keyboard navigation */
*:focus-visible {
  outline: 2px solid var(--color-light);
  outline-offset: 2px;
  border-radius: 2px;
}

/* Remove default focus outline for mouse users */
*:focus:not(:focus-visible) {
  outline: none;
}

/* Button focus styles */
.btn:focus-visible,
.btn-primary:focus-visible,
.btn-outline:focus-visible {
  outline: 3px solid var(--color-light);
  outline-offset: 3px;
}

/* Portfolio item focus styles */
.portfolio-item-modern:focus-visible {
  outline: 3px solid var(--color-light);
  outline-offset: 4px;
  border-radius: var(--radius-md);
}

/* Form input focus styles */
input:focus-visible,
textarea:focus-visible,
select:focus-visible {
  outline: 2px solid var(--color-light);
  outline-offset: 2px;
}

/* Modal close button focus */
.portfolio-modal-close:focus-visible,
.case-study-modal-close:focus-visible {
  outline: 3px solid var(--color-light);
  outline-offset: 3px;
  border-radius: 50%;
}

/* Prevent layout shifts - reserve space for images */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Optimize image rendering */
img[loading="lazy"] {
  content-visibility: auto;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
  font-size: 16px;
}

body {
  font-family: var(--font-sans);
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: none;
  position: relative;
}

@media (max-width: 768px) {
  body {
    cursor: auto;
  }
}

/* ============================================
   SCROLL REVEALING PATTERN
   ============================================ */
.scroll-pattern {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.8s ease-out;
  will-change: opacity;
  contain: layout style paint;
}

.scroll-pattern.active {
  opacity: 1;
}

.scroll-pattern svg {
  width: 100%;
  height: 100%;
}

.scroll-pattern-shape {
  fill: var(--color-light-10);
  opacity: 0;
  transform: translateY(100px);
  transition: all 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.scroll-pattern.active .scroll-pattern-shape {
  opacity: 1;
  transform: translateY(0);
}

.scroll-pattern-shape:nth-child(1) { transition-delay: 0.1s; }
.scroll-pattern-shape:nth-child(2) { transition-delay: 0.3s; }
.scroll-pattern-shape:nth-child(3) { transition-delay: 0.5s; }
.scroll-pattern-shape:nth-child(4) { transition-delay: 0.7s; }
.scroll-pattern-shape:nth-child(5) { transition-delay: 0.9s; }

/* ============================================
   PARTICLE CANVAS
   ============================================ */
.particle-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  opacity: 0.5;
  will-change: contents;
  contain: layout style paint;
}

/* ============================================
   CHRISTMAS FLOATING ICONS
   ============================================ */
.christmas-floating-icons {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  pointer-events: none;
  overflow: visible;
  will-change: transform;
  contain: layout style paint;
  content-visibility: auto;
}

.christmas-icon-wrapper {
  position: absolute;
  will-change: transform;
  transform: translateZ(0);
  backface-visibility: hidden;
  animation: float-gentle infinite ease-in-out;
  contain: layout style paint;
}

.christmas-icon {
  display: block;
  filter: drop-shadow(0 2px 8px rgba(231, 242, 239, 0.6)) drop-shadow(0 0 16px rgba(231, 242, 239, 0.4));
  transition: opacity 0.3s ease;
}

.christmas-icon.snowflake {
  animation: rotate-slow 8s infinite linear;
}

.christmas-icon.star {
  animation: twinkle 3s infinite ease-in-out;
}

.christmas-icon.ornament {
  animation: swing 6s infinite ease-in-out;
}

@keyframes float-gentle {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  25% {
    transform: translateY(-20px) translateX(10px) rotate(5deg);
  }
  50% {
    transform: translateY(-10px) translateX(-5px) rotate(-3deg);
  }
  75% {
    transform: translateY(-25px) translateX(8px) rotate(4deg);
  }
}

@keyframes rotate-slow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes twinkle {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(0.9);
  }
}

@keyframes swing {
  0%, 100% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(5deg);
  }
}

@media (max-width: 768px) {
  .christmas-floating-icons {
    display: none;
  }
}

/* ============================================
   CUSTOM CURSOR
   ============================================ */
.custom-cursor {
  position: fixed;
  width: 20px;
  height: 20px;
  border: 2px solid var(--color-light);
  border-radius: 50%;
  pointer-events: none;
  z-index: 9998;
  transition: transform 0.2s var(--ease-out-expo);
  display: none;
  mix-blend-mode: difference;
}

.cursor-follower {
  position: fixed;
  width: 40px;
  height: 40px;
  border: 1px solid var(--color-light-30);
  border-radius: 50%;
  pointer-events: none;
  z-index: 9997;
  transition: transform 0.4s var(--ease-out-expo);
  display: none;
  mix-blend-mode: difference;
}

body:hover .custom-cursor,
body:hover .cursor-follower {
  display: block;
}

@media (max-width: 768px) {
  .custom-cursor,
  .cursor-follower {
    display: none !important;
  }
}


/* ============================================
   CALL NOW BUTTON (Bottom Right - Full Circle)
   ============================================ */
.call-now-button {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 64px;
  height: 64px;
  background: rgba(5, 10, 31, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 2px solid var(--color-light-30);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  text-decoration: none;
  z-index: 9998;
  transition: all 0.4s var(--ease-out-expo);
  box-shadow: 
    0 4px 20px rgba(8, 18, 44, 0.4),
    0 0 20px rgba(185, 234, 209, 0.2);
}

.call-now-button:hover {
  background: var(--color-light);
  border-color: var(--color-light);
  color: var(--color-dark);
  transform: translateY(-4px) scale(1.05);
  box-shadow: 
    0 8px 30px rgba(185, 234, 209, 0.4),
    0 0 40px rgba(185, 234, 209, 0.3),
    0 0 60px rgba(185, 234, 209, 0.2);
}

.call-now-button:active {
  transform: translateY(-2px) scale(1.02);
}

.call-now-button:focus-visible {
  outline: 3px solid var(--color-light);
  outline-offset: 2px;
}

.call-now-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.call-now-text {
  display: none;
}

@media (max-width: 768px) {
  .call-now-button {
    bottom: calc(5rem + env(safe-area-inset-bottom)) !important;
    right: 1.5rem !important;
    width: 56px !important;
    height: 56px !important;
    z-index: 10000 !important;
  }
  
  .call-now-icon {
    width: 20px;
    height: 20px;
  }
}

/* Old scroll-progress styles removed */
@media (max-width: 768px) {
  .scroll-progress {
    height: 4px !important;
    z-index: 10000 !important;
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
    will-change: width;
  }
  
  /* Reduce particle canvas visibility */
  .particle-canvas {
    display: none !important;
  }
}

/* ============================================
   NAVIGATION - MODERN
   ============================================ */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(5, 10, 31, 0.95);
  backdrop-filter: blur(40px) saturate(180%);
  -webkit-backdrop-filter: blur(40px) saturate(180%);
  border-bottom: 1px solid var(--color-light-10);
  transition: all 0.4s var(--ease-out-expo);
  padding: 1.25rem 0;
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

.navbar.scrolled {
  background: rgba(5, 10, 31, 0.98);
  padding: 1rem 0;
  box-shadow: var(--shadow-sm);
}

.navbar .nav-link {
  color: var(--text-light);
}

.nav-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-logo {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 10;
}

.nav-logo a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;
}

.logo-image {
  height: 48px;
  width: auto;
  transition: all 0.3s var(--ease-out-expo);
  display: block;
  aspect-ratio: auto;
  object-fit: contain;
}

.nav-logo:hover .logo-image {
  transform: scale(1.05);
  opacity: 0.9;
}

.nav-links-wrapper {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-menu {
  display: flex;
  list-style: none;
  gap: 2.5rem;
  align-items: center;
}

.nav-link {
  color: var(--text-light);
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9375rem;
  position: relative;
  transition: color 0.3s var(--ease-out-expo);
  display: flex;
  align-items: center;
}

.nav-link:focus-visible {
  outline: 2px solid var(--color-light);
  outline-offset: 4px;
  border-radius: 4px;
}

.nav-link span {
  position: relative;
  z-index: 1;
}

.nav-link .nav-number {
  font-size: 0.75em;
  font-weight: 600;
  opacity: 0.7;
  margin-right: 0.35em;
}

.nav-link::before {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--color-light);
  transition: width 0.4s var(--ease-out-expo);
  border-radius: 2px;
}

/* Hide underline on mobile */
@media (max-width: 768px) {
  .nav-link::before {
    display: none !important;
    content: none !important;
    width: 0 !important;
    height: 0 !important;
    opacity: 0 !important;
    visibility: hidden !important;
  }
}

.nav-link:hover::before,
.nav-link.active::before {
  width: 100%;
}

.nav-link:hover {
  color: var(--color-light);
}

.nav-link.active {
  color: var(--color-light);
  font-weight: 600;
}

.nav-line {
  width: 1px;
  height: 24px;
  background: var(--color-light-20);
}

/* ============================================
   NAVIGATION DROPDOWN
   ============================================ */
.nav-item-dropdown {
  position: relative;
}

.nav-link .dropdown-arrow {
  margin-left: 0.5rem;
  transition: transform 0.3s var(--ease-out-expo);
}

.nav-link .dropdown-arrow.open {
  transform: rotate(180deg);
}

.nav-dropdown {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(10px);
  background: rgba(5, 10, 31, 0.98);
  backdrop-filter: blur(40px) saturate(180%);
  -webkit-backdrop-filter: blur(40px) saturate(180%);
  border: 1px solid var(--color-light-20);
  border-radius: var(--radius-md);
  padding: 0.5rem 0;
  margin-top: 1rem;
  list-style: none;
  min-width: 180px;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-50%) translateY(0);
  transition: all 0.3s var(--ease-out-expo);
  box-shadow: var(--shadow-lg);
  z-index: 1001;
}

.nav-dropdown.open {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(10px);
}

.nav-dropdown li {
  margin: 0;
}

.nav-dropdown-link {
  display: block;
  padding: 0.75rem 1.5rem;
  color: var(--text-light);
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9375rem;
  transition: all 0.3s var(--ease-out-expo);
  position: relative;
}

.nav-dropdown-link::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 3px;
  height: 100%;
  background: var(--color-light);
  opacity: 0;
  transition: opacity 0.3s var(--ease-out-expo);
}

.nav-dropdown-link:hover,
.nav-dropdown-link.active {
  background: var(--color-light-10);
  color: var(--color-light);
  padding-left: 2rem;
}

.nav-dropdown-link:hover::before,
.nav-dropdown-link.active::before {
  opacity: 1;
}

@media (max-width: 768px) {
  .nav-dropdown {
    position: static;
    transform: none;
    margin-top: 0.5rem;
    border-radius: var(--radius-sm);
    min-width: auto;
    width: 100%;
  }

  .nav-dropdown.open {
    transform: none;
  }

  .nav-dropdown-link {
    padding: 0.625rem 1rem;
  }

  .nav-dropdown-link:hover,
  .nav-dropdown-link.active {
    padding-left: 1.5rem;
  }
}

.nav-toggle {
  display: none;
  position: relative;
  width: 56px;
  height: 56px;
  background: rgba(180, 232, 201, 0.1);
  border: 2px solid var(--color-light-30);
  border-radius: 50%;
  cursor: pointer;
  padding: 0;
  z-index: 1002;
  transition: all 0.4s var(--ease-out-expo);
  overflow: hidden;
  align-items: center;
  justify-content: center;
  pointer-events: all !important;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  -webkit-user-select: none;
}

.nav-toggle::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at center, var(--color-light-20) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.4s var(--ease-out-expo);
  border-radius: 50%;
  pointer-events: none;
}

.nav-toggle:hover {
  background: rgba(180, 232, 201, 0.2);
  border-color: var(--color-light-50);
  transform: scale(1.05);
  box-shadow: 
    0 0 0 4px rgba(182, 213, 195, 0.1),
    0 8px 24px rgba(182, 213, 195, 0.2);
}

.nav-toggle:hover::before {
  opacity: 1;
}

.nav-toggle.active {
  background: var(--color-light);
  border-color: var(--color-light);
  box-shadow: 
    0 0 0 4px rgba(182, 213, 195, 0.2),
    0 12px 32px rgba(182, 213, 195, 0.3);
}

.nav-toggle.active::before {
  opacity: 0;
}

.nav-toggle-inner {
  position: relative;
  width: 24px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  pointer-events: none;
}

.toggle-line {
  width: 24px;
  height: 3px;
  background: var(--color-light);
  border-radius: 3px;
  transition: all 0.4s var(--ease-out-expo);
  position: relative;
  transform-origin: center;
}

.nav-toggle.active .toggle-line:nth-child(1) {
  transform: translateY(8.5px) rotate(45deg);
  background: var(--color-dark);
}

.nav-toggle.active .toggle-line:nth-child(2) {
  opacity: 0;
  transform: translateX(-10px);
}

.nav-toggle.active .toggle-line:nth-child(3) {
  transform: translateY(-8.5px) rotate(-45deg);
  background: var(--color-dark);
}

.toggle-circle {
  position: absolute;
  width: 0;
  height: 0;
  background: var(--color-light);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.5s var(--ease-out-expo);
  opacity: 0;
}

.nav-toggle.active .toggle-circle {
  width: 120%;
  height: 120%;
  opacity: 0.1;
}

/* ============================================
   HERO SECTION - OUTSTANDING
   ============================================ */
.hero {
  min-height: auto;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 2rem 2rem;
  overflow: hidden;
  z-index: 2;
  background: var(--bg-light);
}

.hero-background {
  position: absolute;
  inset: 0;
  z-index: 0;
  overflow: hidden;
}

.hero-gradient-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, var(--color-light) 0%, rgba(180, 232, 201, 0.95) 100%);
}

.hero-blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.2;
  animation: blobFloat 20s ease-in-out infinite;
  will-change: transform;
  contain: layout style paint;
}

.blob-1 {
  width: 500px;
  height: 500px;
