}

/* Staggered delays for stat numbers */
.stat-box:nth-child(1) .stat-number {
  animation-delay: 0.2s;
}

.stat-box:nth-child(3) .stat-number {
  animation-delay: 0.4s;
}

.stat-box:nth-child(5) .stat-number {
