  .process-steps.process-carousel {
    display: flex !important;
    flex-direction: column;
    overflow: hidden;
    position: relative;
  }

  .process-carousel .process-carousel-track {
    display: flex;
    flex-direction: row;
    transition: transform 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    will-change: transform;
  }

  .process-carousel .process-step {
    flex: 0 0 100%;
    min-width: 100%;
    border-right: none;
    border-bottom: none;
  }

  .process-carousel .process-step:last-child {
    border-bottom: none;
  }

  .process-carousel-dots {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    padding: 1.25rem 0;
  }

  .process-carousel-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    border: none;
    background: var(--color-light-30);
    cursor: pointer;
    transition: all 0.3s ease;
    padding: 0;
