   ============================================ */
.testimonials {
  background: linear-gradient(180deg, var(--bg-secondary) 0%, rgba(5, 10, 31, 0.98) 50%, var(--bg-secondary) 100%);
  color: var(--text-light);
  position: relative;
  overflow: hidden;
  padding: 8rem 0;
  border-top: 1px solid var(--color-light-05);
  border-bottom: 1px solid var(--color-light-05);
}

.testimonials .section-title .title-main {
  color: var(--text-light);
}

/* Testimonials Carousel */
.testimonials-carousel-wrapper {
  position: relative;
  margin-top: 4rem;
  overflow: hidden;
  width: 100%;
  box-sizing: border-box;
}

.testimonials-carousel-desktop {
  position: relative;
}

/* Desktop carousel track - no animation, uses transform */
.testimonials-carousel-track.desktop-carousel {
  display: flex;
  gap: 2rem;
  width: 100%;
  transition: transform 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Mobile: single-card view with dots (no horizontal scroll strip like Trusted By) */
.testimonials-mobile-view {
  display: none;
  margin-top: 2rem;
  overflow: hidden;
  width: 100%;
}

.testimonials-mobile-track {
  display: flex;
  width: 100%;
  transition: transform 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.testimonials-mobile-track .testimonial-card {
  flex: 0 0 100%;
  min-width: 0;
  width: 100%;
}

.testimonials-carousel-track.mobile-scroll {
  display: none !important;
}

@media (max-width: 768px) {
  .testimonials-carousel-desktop {
    display: none !important;
  }
  
  .testimonials-mobile-view.mobile-only {
    display: block !important;
  }
}

.testimonial-card {
  flex-shrink: 0;
  background: rgba(5, 10, 31, 0.6);
  border: 1px solid var(--color-light-10);
  border-left: 4px solid var(--color-light);
  border-radius: 0 var(--radius-lg) var(--radius-lg) 0;
  padding: 2rem 2rem 2rem 1.75rem;
  transition: border-color 0.35s ease, box-shadow 0.35s ease;
  position: relative;
  box-sizing: border-box;
  backdrop-filter: blur(12px);
  box-shadow: 0 4px 24px rgba(5, 10, 31, 0.25);
  transform: translateZ(0);
  backface-visibility: hidden;
  contain: layout style paint;
}

/* Desktop carousel cards */
.testimonials-carousel-track.desktop-carousel .testimonial-card {
  flex: 0 0 calc(33.333% - 1.33rem);
  min-width: 0;
  max-width: 100%;
  width: 100%;
}

.testimonial-card:hover {
  border-left-color: var(--color-light);
  box-shadow: 0 8px 32px rgba(5, 10, 31, 0.35), 0 0 0 1px rgba(185, 234, 209, 0.08);
}

.testimonial-card::before {
  display: none;
}

/* Large Desktop (1440px+) */
@media (min-width: 1440px) {
  .testimonials-carousel-track.desktop-carousel .testimonial-card {
    padding: 2.5rem 2.5rem 2.5rem 2rem;
  }
  
  .testimonials-carousel-track.desktop-carousel {
    gap: 2.5rem;
  }
}

/* Desktop (1024px - 1439px) */
/* Uses default flex calculation from .testimonials-carousel-track.desktop-carousel .testimonial-card */

/* Tablet (768px - 1024px) */
@media (max-width: 1024px) and (min-width: 769px) {
  .hero-actions {
    justify-content: center;
  }

  .testimonials-carousel-track.desktop-carousel .testimonial-card {
    padding: 1.75rem 1.75rem 1.75rem 1.5rem;
  }
  
  .testimonials-carousel-track.desktop-carousel {
    gap: 1.5rem;
  }
}

/* Mobile (up to 768px) */
@media (max-width: 768px) {
  .testimonial-nav-btn.desktop-only {
    display: none !important;
  }
  
  .testimonials-dots.desktop-only {
    display: none !important;
  }
  
  .testimonials-mobile-track .testimonial-card {
    padding: 1.5rem 1.25rem 1.5rem 1.25rem;
  }
}

.testimonials-dots-mobile {
  margin-top: 1.5rem;
}

.testimonial-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.testimonial-quote {
  color: var(--color-light-30);
  margin-bottom: 0.25rem;
}

.testimonial-quote svg {
  width: 48px;
  height: 48px;
}

.testimonial-rating {
  display: flex;
  gap: 0.25rem;
  margin-bottom: 0.75rem;
  color: var(--color-light);
}

.testimonial-rating svg {
  width: 18px;
  height: 18px;
  fill: var(--color-light);
}

.testimonial-text {
  color: var(--text-on-dark);
  font-size: 1rem;
  line-height: 1.7;
  margin: 0 0 1.25rem 0;
  font-style: italic;
  flex: 1;
}

.testimonial-author {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  margin-top: auto;
}

.testimonial-avatar {
  flex-shrink: 0;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--color-light-20);
  transition: border-color 0.3s ease;
}

.testimonial-card:hover .testimonial-avatar {
  border-color: var(--color-light-40);
}

.testimonial-avatar-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

.testimonial-author-info {
  display: flex;
  flex-direction: column;
  gap: 0.15rem;
  min-width: 0;
}

.testimonial-author h3 {
  font-family: var(--font-serif);
  font-size: 1.0625rem;
  font-weight: 700;
  color: var(--text-light);
  margin: 0;
}

.testimonial-author span {
  color: var(--text-tertiary);
  font-size: 0.8125rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}


/* Carousel Navigation */
.testimonial-nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(25, 24, 59, 0.8);
  border: 1px solid var(--color-light-20);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--color-light);
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 10;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(8, 18, 44, 0.2);
}

.testimonial-nav-btn.desktop-only {
  display: flex;
}

.testimonial-nav-btn:hover {
  background: rgba(180, 232, 201, 0.2);
  border-color: var(--color-light-40);
  transform: translateY(-50%) scale(1.1);
  box-shadow: 0 6px 20px rgba(182, 213, 195, 0.3);
}

.testimonial-nav-btn:active {
  transform: translateY(-50%) scale(0.95);
}

.testimonial-nav-btn svg {
  width: 20px;
  height: 20px;
  stroke-width: 2.5;
}

.testimonial-nav-prev {
  left: 0;
}

.testimonial-nav-next {
  right: 0;
}

@media (max-width: 768px) {
  .testimonial-nav-btn {
    width: 40px;
    height: 40px;
  }
  
  .testimonial-nav-btn svg {
    width: 16px;
    height: 16px;
  }
  
  .testimonial-nav-prev {
    left: -10px;
  }
  
  .testimonial-nav-next {
    right: -10px;
  }
}

/* Dot Indicators */
.testimonials-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  margin-top: 2.5rem;
  flex-wrap: wrap;
}

.testimonials-dots.desktop-only {
  display: flex;
}

.testimonial-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--color-light-20);
  border: none;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  padding: 0;
  position: relative;
}

.testimonial-dot::before {
  content: '';
  position: absolute;
  inset: -4px;
  border-radius: 50%;
  background: transparent;
  transition: all 0.3s ease;
}

.testimonial-dot:hover::before {
  background: var(--color-light-10);
}

.testimonial-dot:hover {
  background: var(--color-light-40);
  transform: scale(1.2);
}

.testimonial-dot.active {
  background: var(--color-light);
  width: 32px;
  border-radius: 6px;
  box-shadow: 0 0 15px rgba(182, 213, 195, 0.5);
}

.testimonial-dot.active::before {
  display: none;
}

/* ============================================
   TRUSTED BY SECTION
   ============================================ */
.trusted-by {
  background: var(--bg-secondary);
  color: var(--text-light);
  position: relative;
  overflow: hidden;
  padding: 8rem 0;
}

.trusted-by::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 50% 50%, rgba(182, 213, 195, 0.05) 0%, transparent 70%);
  pointer-events: none;
  z-index: 0;
}

.trusted-by .section-title .title-main {
  color: var(--text-light);
}

.trusted-by-grid {
  position: relative;
  width: 100%;
  margin-top: 5rem;
  overflow: hidden;
  mask: linear-gradient(
    to right,
    transparent,
    black 10%,
    black 90%,
    transparent
  );
  -webkit-mask: linear-gradient(
    to right,
    transparent,
    black 10%,
    black 90%,
    transparent
  );
  z-index: 1;
}

.trusted-by-track {
  display: flex;
  gap: 4rem;
  width: max-content;
  animation: slideLeft 20s linear infinite;
}

.trusted-by-track:hover {
  animation-play-state: paused;
}

@keyframes slideLeft {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-25%);
  }
}

.trusted-by-card {
  flex-shrink: 0;
  width: 160px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.trusted-card-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  text-decoration: none;
  color: var(--text-light);
  transition: all 0.3s;
}

.trusted-card-link:hover {
  color: var(--color-light);
}

.trusted-company-name {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--color-light-80);
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  transition: all 0.3s;
  white-space: nowrap;
}

.trusted-by-card:hover .trusted-company-name {
  color: var(--color-light);
  transform: translateY(-2px);
}

.trusted-card-inner {
  position: relative;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(185, 234, 209, 0.03);
  border: 2px solid var(--color-light);
  padding: 1rem;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
  box-shadow: 0 4px 20px rgba(5, 10, 31, 0.1);
}

.trusted-card-inner::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 50%;
  background: conic-gradient(
    from 0deg,
    transparent 0%,
    var(--color-light-10) 50%,
    transparent 100%
  );
  opacity: 0;
  transition: opacity 0.5s;
  animation: rotateBorder 3s linear infinite;
}

.trusted-by-card:hover .trusted-card-inner::before {
  opacity: 0.4;
}

@keyframes rotateBorder {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.trusted-card-inner::after {
  content: '';
  position: absolute;
  inset: 3px;
  border-radius: 50%;
  background: var(--bg-primary);
  z-index: -1;
}

.trusted-by-card:hover .trusted-card-inner {
  background: rgba(185, 234, 209, 0.06);
  border-color: var(--color-light);
  box-shadow: 0 15px 40px rgba(8, 18, 44, 0.3);
  transform: scale(1.15);
}

.trusted-logo-wrapper {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.trusted-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  border-radius: 50%;
  opacity: 0.85;
  transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
  aspect-ratio: auto;
}

.trusted-by-card:hover .trusted-logo {
  opacity: 1;
  transform: scale(1.1);
}

.trusted-card-overlay {
  display: none;
}

@media (max-width: 1024px) {
  .trusted-by-card {
    width: 140px;
  }

  .trusted-card-inner {
    width: 140px;
    height: 140px;
  }

  .trusted-by-track {
    gap: 3rem;
  }
}

@media (max-width: 768px) {
  .trusted-by-card {
    width: 120px;
  }

  .trusted-card-inner {
    width: 120px;
    height: 120px;
  }

  .trusted-company-name {
    font-size: 0.75rem;
  }

  .trusted-by-track {
    gap: 2.5rem;
  }

  @keyframes slideLeft {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-25%);
    }
  }
}

@media (max-width: 480px) {
  .trusted-by-card {
    width: 100px;
  }

  .trusted-card-inner {
    width: 100px;
    height: 100px;
  }

  .trusted-company-name {
    font-size: 0.6875rem;
  }

  .trusted-by-track {
    gap: 2rem;
  }
}

/* ============================================
   FAQ SECTION
   ============================================ */
.faq {
  background: var(--bg-secondary);
  color: var(--text-light);
  position: relative;
  overflow: hidden;
  padding: 8rem 0;
}

.faq .section-title .title-main {
  color: var(--text-light);
}

.faq-wrapper {
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 4rem;
}

.faq-grid {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

/* FAQ Carousel Styles */
.faq-carousel-container {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.faq-carousel-track {
  display: flex;
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform;
}

.faq-carousel-slide {
  min-width: 100%;
  width: 100%;
  flex-shrink: 0;
  padding: 0;
  box-sizing: border-box;
}

.faq-carousel-slide .faq-item {
  margin: 0;
  width: 100%;
}

.faq-carousel-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  margin-top: 2.5rem;
  padding: 0 1rem;
}

.faq-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid var(--color-light-30);
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
  outline: none;
}

.faq-dot:hover {
  border-color: var(--color-light-50);
  transform: scale(1.2);
}

.faq-dot.active {
  background: var(--color-light);
  border-color: var(--color-light);
  width: 12px;
  height: 12px;
}

.faq-item {
  background: var(--bg-primary);
  border: 1px solid var(--color-light-10);
  border-radius: var(--radius-lg);
  overflow: hidden;
  transition: all 0.4s var(--ease-out-expo);
  position: relative;
}

.faq-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--color-light);
  opacity: 0;
  transition: opacity 0.4s var(--ease-out-expo);
  border-radius: 2px 0 0 2px;
}

.faq-item:hover {
  border-color: var(--color-light-20);
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.faq-item.active {
  border-color: var(--color-light-30);
  box-shadow: var(--shadow-lg);
}

.faq-item.active::before {
  opacity: 1;
}

.faq-question {
  width: 100%;
  padding: 1.75rem 2rem;
  background: transparent;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  text-align: left;
  transition: all 0.3s var(--ease-out-expo);
  color: var(--text-light);
  gap: 1.5rem;
}

.faq-question:hover {
  background: var(--color-light-05);
}

.faq-question.active {
  background: var(--color-light-05);
  padding-bottom: 1.25rem;
}

.faq-question-text {
  font-family: var(--font-serif);
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-light);
  line-height: 1.4;
  flex: 1;
  transition: color 0.3s var(--ease-out-expo);
}

.faq-item.active .faq-question-text {
  color: var(--color-light);
}

.faq-icon {
  width: 24px;
  height: 24px;
  color: var(--color-light);
  transition: all 0.4s var(--ease-out-expo);
  flex-shrink: 0;
  transform: rotate(0deg);
}

.faq-item.active .faq-icon {
  transform: rotate(180deg);
  color: var(--color-light);
}

.faq-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s var(--ease-out-expo);
}

.faq-item.active .faq-answer {
  max-height: 500px;
}

.faq-answer-content {
  padding: 0 2rem 1.75rem 2rem;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.4s var(--ease-out-expo) 0.1s;
}

.faq-item.active .faq-answer-content {
  opacity: 1;
  transform: translateY(0);
}

.faq-answer-content p {
  font-size: 1.0625rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin: 0;
}

.faq-answer-content p:not(:last-child) {
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .faq {
    padding: 2rem 0;
  }

  .faq-wrapper {
    padding-top: 3rem;
  }

  .faq-grid {
    gap: 1rem;
  }

  .faq-carousel-container {
    overflow: hidden;
  }

  .faq-carousel-dots {
    margin-top: 2rem;
    gap: 0.5rem;
  }

  .faq-dot {
    width: 8px;
    height: 8px;
  }

  .faq-dot.active {
    width: 10px;
    height: 10px;
  }

  .faq-question {
    padding: 1.5rem 1.5rem;
    gap: 1rem;
  }

  .faq-question.active {
    padding-bottom: 1rem;
  }

  .faq-question-text {
    font-size: 1.125rem;
  }

  .faq-icon {
    width: 20px;
    height: 20px;
  }

  .faq-answer-content {
    padding: 0 1.5rem 1.5rem 1.5rem;
  }

  .faq-answer-content p {
    font-size: 1rem;
  }
}

/* ============================================
   CONTACT SECTION
   ============================================ */
.contact {
  background: var(--bg-primary);
  color: var(--text-light);
  position: relative;
  overflow: hidden;
  padding: 8rem 0 0 0;
}

.contact-content-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  margin-top: 4rem;
  align-items: start;
}

.contact-form-wrapper {
  width: 100%;
}

.contact .section-title .title-main {
  color: var(--text-light);
}

.contact-subtitle {
  font-size: 1.125rem;
  color: var(--color-light-80);
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 4rem;
  line-height: 1.6;
}

.social-modern {
  display: flex;
  gap: 1rem;
}

.social-item {
  width: 48px;
  height: 48px;
  background: rgba(185, 234, 209, 0.03);
  border: 1px solid var(--color-light-10);
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  text-decoration: none;
  transition: all 0.4s var(--ease-out-expo);
}

.social-item svg {
  width: 20px;
  height: 20px;
  transition: transform 0.4s;
}

.social-item:hover {
  background: var(--color-light);
  border-color: var(--color-light);
  color: var(--color-dark);
  transform: translateY(-4px) rotate(5deg);
}

.social-item:hover svg {
  transform: scale(1.2);
}

/* Inline success state after form submit */
#contact .contact-form-success-inline {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  background: rgba(180, 232, 201, 0.15);
  border: 1px solid var(--color-light-30);
  border-radius: var(--radius-md);
  color: var(--color-light);
  font-size: 0.875rem;
  font-weight: 600;
  animation: contactFormSuccessIn 0.4s var(--ease-out-expo);
}

#contact .contact-form-success-check {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  background: var(--color-light);
  color: var(--color-dark);
  border-radius: 50%;
  font-size: 0.875rem;
  font-weight: 700;
  animation: contactFormSuccessCheckPop 0.5s 0.2s var(--ease-out-expo) both;
}

@keyframes contactFormSuccessIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes contactFormSuccessCheckPop {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.contact-form-simple {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* Compact contact form to match left column height */
#contact .contact-form-simple {
  gap: 1.25rem;
}

#contact .contact-form-simple.form-submitted {
  opacity: 0.7;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.contact-form-simple .form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

#contact .contact-form-simple .form-row {
  gap: 1rem;
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

#contact .form-field {
  gap: 0.375rem;
}

.form-field label {
  font-size: 1rem;
  font-weight: 600;
  color: var(--color-light);
  text-transform: capitalize;
}

#contact .form-field label {
  font-size: 0.8125rem;
}

.form-field input,
.form-field textarea {
  width: 100%;
  padding: 1rem;
  background: transparent;
  border: none;
  border-bottom: 2px solid var(--color-light-20);
  color: var(--text-light);
  font-family: var(--font-sans);
  font-size: 1rem;
  transition: all 0.3s;
  resize: vertical;
}

#contact .form-field input,
#contact .form-field textarea {
  padding: 0.5rem 0;
  font-size: 0.9375rem;
}

.form-field input:focus,
.form-field input:focus-visible,
.form-field textarea:focus,
.form-field textarea:focus-visible {
  outline: none;
  border-bottom-color: var(--color-light);
}

.form-field textarea {
  min-height: 120px;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

#contact .form-field textarea {
  min-height: 80px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.form-field textarea::placeholder {
  color: var(--color-light-50);
  opacity: 1;
}

.btn-contact-submit {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem 2.5rem;
  background: transparent;
  border: 2px solid var(--color-light);
  border-radius: var(--radius-full);
  color: var(--color-light);
  font-family: var(--font-sans);
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.4s var(--ease-out-expo);
  margin-top: 1rem;
  align-self: center;
}

.btn-contact-submit:hover {
  background: var(--color-light);
  color: var(--color-dark);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(185, 234, 209, 0.3);
}

.btn-contact-submit span:last-child {
  font-size: 1.25rem;
  transition: transform 0.3s;
}

.btn-contact-submit:hover span:last-child {
  transform: translateX(4px);
}

.btn-contact-submit:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.btn-contact-submit:disabled:hover {
  background: transparent;
  color: var(--color-light);
  transform: none;
}

#contact .btn-contact-submit {
  padding: 0.625rem 1.5rem;
  font-size: 0.875rem;
  margin-top: 0.5rem;
}

#contact .btn-contact-submit span:last-child {
  font-size: 1rem;
}

/* ============================================
   CONTACT INFO SECTION
   ============================================ */
.contact-info-wrapper {
  width: 100%;
  position: sticky;
  top: 120px;
}

.contact-info-header {
  margin-bottom: 2.5rem;
}

.contact-info-title {
  font-family: var(--font-serif);
  font-size: 2rem;
  font-weight: 800;
  color: var(--color-light);
  margin-bottom: 0.75rem;
}

.contact-info-subtitle {
  font-size: 1rem;
  color: var(--text-secondary);
  line-height: 1.6;
}

.contact-info-cards {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.contact-info-card {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 1.75rem 2rem;
  background: var(--bg-secondary);
  border: 1px solid var(--color-light-10);
  border-radius: var(--radius-lg);
  text-decoration: none;
  color: var(--text-light);
  transition: all 0.4s var(--ease-out-expo);
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  overflow: hidden;
}

.contact-info-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--color-light);
  opacity: 0;
  transition: opacity 0.4s var(--ease-out-expo);
  border-radius: 2px 0 0 2px;
}

.contact-info-card:hover,
.contact-info-card:focus,
.contact-info-card:active {
  border-color: var(--color-light-30);
  background: var(--color-light-05);
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  outline: none;
}

.contact-info-card:hover::before,
.contact-info-card:focus::before,
.contact-info-card:active::before {
  opacity: 1;
}

.contact-info-icon {
  width: 56px;
  height: 56px;
  min-width: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-light-10);
  border: 1px solid var(--color-light-20);
  border-radius: var(--radius-md);
  color: var(--color-light);
  transition: all 0.4s var(--ease-out-expo);
  flex-shrink: 0;
}

.contact-info-card:hover .contact-info-icon,
.contact-info-card:focus .contact-info-icon,
.contact-info-card:active .contact-info-icon {
  background: var(--color-light);
  border-color: var(--color-light);
  color: var(--color-dark);
  transform: scale(1.1) rotate(5deg);
  box-shadow: var(--shadow-light);
}

.contact-info-icon svg {
  width: 24px;
  height: 24px;
  transition: transform 0.4s var(--ease-out-expo);
}

.contact-info-card:hover .contact-info-icon svg,
.contact-info-card:focus .contact-info-icon svg,
.contact-info-card:active .contact-info-icon svg {
  transform: scale(1.1);
}

.contact-info-content {
  display: flex;
  flex-direction: column;
  gap: 0.375rem;
  flex: 1;
}

.contact-info-label {
  font-size: 0.8125rem;
  font-weight: 600;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: color 0.3s var(--ease-out-expo);
}

.contact-info-card:hover .contact-info-label,
.contact-info-card:focus .contact-info-label,
.contact-info-card:active .contact-info-label {
  color: var(--color-light-80);
}

.contact-info-value {
  font-size: 1.0625rem;
  font-weight: 600;
  color: var(--text-light);
  line-height: 1.4;
  word-break: break-word;
  transition: color 0.3s var(--ease-out-expo);
}

.contact-info-card:hover .contact-info-value,
.contact-info-card:focus .contact-info-value,
.contact-info-card:active .contact-info-value {
  color: var(--color-light);
}

.contact-info-arrow {
  width: 24px;
  height: 24px;
  min-width: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light-30);
  transition: all 0.4s var(--ease-out-expo);
  flex-shrink: 0;
  opacity: 0;
  transform: translateX(-10px);
}

.contact-info-card:hover .contact-info-arrow,
.contact-info-card:focus .contact-info-arrow,
.contact-info-card:active .contact-info-arrow {
  opacity: 1;
  transform: translateX(0);
  color: var(--color-light);
}

.contact-info-arrow svg {
  width: 20px;
  height: 20px;
}

/* Vertical contact/social links with info - Desktop & Tablet only */
.contact-links-vertical {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
}

.contact-link-vertical {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.625rem 1rem;
  background: rgba(185, 234, 201, 0.05);
  border: 1px solid var(--color-light-10);
  border-radius: var(--radius-md);
  color: var(--color-light);
  text-decoration: none;
  transition: all 0.3s var(--ease-out-expo);
}

.contact-link-vertical:hover,
.contact-link-vertical:focus {
  background: var(--color-light-10);
  border-color: var(--color-light-20);
  transform: translateX(6px);
}

.contact-link-icon {
  width: 32px;
  height: 32px;
  min-width: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-light-05);
  border: 1px solid var(--color-light-10);
  border-radius: var(--radius-md);
}

.contact-link-vertical:hover .contact-link-icon,
.contact-link-vertical:focus .contact-link-icon {
  background: var(--color-light);
  border-color: var(--color-light);
  color: var(--color-dark);
}

.contact-link-content {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  gap: 0.125rem;
}

.contact-link-label {
  font-size: 0.6875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--color-light-80);
}

.contact-link-value {
  font-size: 0.8125rem;
  font-weight: 600;
  color: var(--color-light);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.contact-link-vertical:hover .contact-link-value,
.contact-link-vertical:focus .contact-link-value {
  color: var(--color-light);
}

.contact-link-arrow {
  opacity: 0.5;
  transition: all 0.3s;
}

.contact-link-vertical:hover .contact-link-arrow,
.contact-link-vertical:focus .contact-link-arrow {
  opacity: 1;
  transform: translateX(4px);
}


@media (max-width: 1024px) {
  .contact-content-wrapper {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .contact-info-wrapper {
    position: static;
  }

  /* Keep 2-column layout for contact form + minimized links on tablet */
  #contact .contact-content-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  /* Single column on mobile - form hidden, full cards only */
  #contact .contact-content-wrapper {
    grid-template-columns: 1fr !important;
  }

  .contact-content-wrapper {
    gap: 2.5rem;
  }

  .contact-info-header {
    margin-bottom: 2rem;
  }

  .contact-info-title {
    font-size: 1.75rem;
  }

  .contact-info-cards {
    gap: 1rem;
  }

  .contact-info-card {
    padding: 1.5rem 1.5rem;
    gap: 1.25rem;
  }

  .contact-info-icon {
    width: 48px;
    height: 48px;
    min-width: 48px;
  }

  .contact-info-icon svg {
    width: 20px;
    height: 20px;
  }

  .contact-info-value {
    font-size: 1rem;
  }
}

/* ============================================
   FORM MODAL MESSAGES
   ============================================ */
.form-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.4s var(--ease-out-expo), visibility 0.4s;
}

.form-modal.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.form-modal-overlay {
  position: absolute;
  inset: 0;
  background: rgba(13, 27, 57, 0.85);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  opacity: 0;
  transition: opacity 0.4s var(--ease-out-expo);
}

.form-modal.active .form-modal-overlay {
  opacity: 1;
}

.form-modal-container {
  position: relative;
  width: 90%;
  max-width: 500px;
  background: var(--bg-secondary);
  border: 2px solid var(--color-light-20);
  border-radius: var(--radius-xl);
  padding: 3rem 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  text-align: center;
  transform: scale(0.9) translateY(20px);
  transition: transform 0.4s var(--ease-out-expo);
  box-shadow: 
    0 20px 60px rgba(8, 18, 44, 0.5),
    0 0 40px rgba(185, 234, 209, 0.1);
  z-index: 1;
}

.form-modal.active .form-modal-container {
  transform: scale(1) translateY(0);
}

/* Success state: clear feedback + animation */
.form-modal-success .form-modal-container {
  border-color: var(--color-light-40);
  box-shadow:
    0 20px 60px rgba(8, 18, 44, 0.5),
    0 0 60px rgba(180, 232, 201, 0.15);
}

.form-modal-success .form-modal-title {
  color: var(--color-light);
}

.form-success-icon {
  animation: formSuccessIconPop 0.55s var(--ease-spring) both;
}

.form-success-checkmark {
  stroke-dasharray: 30;
  stroke-dashoffset: 30;
  animation: formSuccessCheckDraw 0.5s 0.25s var(--ease-out-expo) forwards;
}

@keyframes formSuccessIconPop {
  from {
    opacity: 0;
    transform: scale(0.4);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes formSuccessCheckDraw {
  to {
    stroke-dashoffset: 0;
  }
}

.form-modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 40px;
  height: 40px;
  background: var(--color-light-10);
  border: 1px solid var(--color-light-20);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  cursor: pointer;
  transition: all 0.3s var(--ease-out-expo);
  z-index: 2;
}

.form-modal-close:hover {
  background: var(--color-light);
  border-color: var(--color-light);
  color: var(--color-dark);
  transform: rotate(90deg);
}

.form-modal-close svg {
  width: 18px;
  height: 18px;
}

.form-modal-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  animation: iconPulse 0.6s var(--ease-out-expo);
  flex-shrink: 0;
}

.success-icon {
  background: linear-gradient(135deg, var(--color-light) 0%, rgba(180, 232, 201, 0.8) 100%);
  color: var(--color-dark);
  box-shadow: 
    0 0 0 0 rgba(185, 234, 209, 0.4),
    0 10px 30px rgba(185, 234, 209, 0.3);
  animation: iconPulse 0.6s var(--ease-out-expo), successGlow 2s ease-in-out infinite 0.6s;
}

.warning-icon {
  background: linear-gradient(135deg, var(--color-light) 0%, rgba(182, 213, 195, 0.6) 100%);
  color: var(--color-dark);
  box-shadow: 
    0 0 0 0 rgba(185, 234, 209, 0.4),
    0 10px 30px rgba(185, 234, 209, 0.2);
  animation: iconPulse 0.6s var(--ease-out-expo);
}

.validation-icon {
  background: linear-gradient(135deg, var(--color-light) 0%, rgba(182, 213, 195, 0.6) 100%);
  color: var(--color-dark);
  box-shadow: 
    0 0 0 0 rgba(185, 234, 209, 0.4),
    0 10px 30px rgba(185, 234, 209, 0.2);
  animation: iconPulse 0.6s var(--ease-out-expo);
}

.form-modal-icon svg {
  width: 48px;
  height: 48px;
  stroke-width: 2.5;
}

.form-modal-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}

.form-modal-title {
  font-family: var(--font-serif);
  font-size: 1.75rem;
  font-weight: 800;
  color: var(--text-light);
  margin: 0;
  line-height: 1.3;
}

.form-modal-message {
  font-size: 1.0625rem;
  line-height: 1.7;
  color: var(--text-secondary);
  margin: 0;
}

@keyframes iconPulse {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes successGlow {
  0%, 100% {
    box-shadow: 
      0 0 0 0 rgba(185, 234, 209, 0.4),
      0 10px 30px rgba(185, 234, 209, 0.3);
  }
  50% {
    box-shadow: 
      0 0 0 10px rgba(185, 234, 209, 0),
      0 10px 30px rgba(185, 234, 209, 0.4);
  }
}

@media (max-width: 768px) {
  .form-modal-container {
    width: 95%;
    max-width: 400px;
    padding: 2.5rem 2rem;
    gap: 1.25rem;
  }

  .form-modal-close {
    top: 1rem;
    right: 1rem;
    width: 36px;
    height: 36px;
  }

  .form-modal-icon {
    width: 70px;
    height: 70px;
  }

  .form-modal-icon svg {
    width: 40px;
    height: 40px;
  }

  .form-modal-title {
    font-size: 1.5rem;
  }

  .form-modal-message {
    font-size: 1rem;
  }
}

/* ============================================
   FOOTER
   ============================================ */
.footer-modern {
  background: var(--bg-primary);
  color: var(--text-light);
  padding: 2.5rem 0;
  border-top: 1px solid var(--color-light-10);
  position: relative;
  z-index: 2;
  width: 100%;
  clear: both;
}

.footer-content-modern {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.footer-left p {
  font-size: 0.9375rem;
  opacity: 0.8;
  margin: 0;
}

.footer-links {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.footer-link {
  background: none;
  border: none;
  color: var(--text-light);
  font-size: 0.9375rem;
  font-family: var(--font-sans);
  opacity: 0.8;
  cursor: pointer;
  padding: 0.25rem 0;
  transition: all 0.3s var(--ease-out-expo);
  text-decoration: none;
  position: relative;
  outline: none;
}

.footer-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background: var(--color-light);
  transition: width 0.3s var(--ease-out-expo);
}

.footer-link:hover,
.footer-link:focus {
  opacity: 1;
  color: var(--color-light);
}

.footer-link:hover::after,
.footer-link:focus::after {
  width: 100%;
}

.footer-link:focus {
  outline: 2px solid var(--color-light);
  outline-offset: 4px;
  border-radius: 2px;
}

.footer-link-divider {
  color: var(--text-light);
  opacity: 0.5;
  font-size: 0.9375rem;
}

.footer-link-anchor {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  display: inline-block;
}

.footer-social {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.footer-social .social-modern {
  gap: 0.75rem;
}

.footer-social .social-item {
  width: 40px;
  height: 40px;
  background: rgba(185, 234, 209, 0.05);
  border: 1px solid var(--color-light-10);
}

.footer-social .social-item svg {
  width: 18px;
  height: 18px;
}

.footer-social .social-icon-img {
  width: 18px;
  height: 18px;
}

.heart-beat {
  color: var(--color-light);
  animation: heartbeat 1.5s ease-in-out infinite;
  display: inline-block;
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

/* ============================================
   ANIMATIONS
   ============================================ */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(40px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-40px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

