/* ============================================
   PROJECT REQUEST MODAL (NEW)
   ============================================ */
.project-request-modal-wrapper {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  z-index: 10000 !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  padding: 2rem !important;
  visibility: visible !important;
  opacity: 1 !important;
  pointer-events: auto !important;
}

.project-request-modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(5, 10, 31, 0.92);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  animation: backdropFadeIn 0.3s ease-out;
}

@keyframes backdropFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.project-request-modal-content {
  position: relative !important;
  background: rgba(25, 24, 59, 0.75) !important;
  backdrop-filter: blur(20px) saturate(180%) !important;
  -webkit-backdrop-filter: blur(20px) saturate(180%) !important;
  border-radius: 20px !important;
  width: 100% !important;
  max-width: 800px !important;
  max-height: 90vh !important;
  overflow-y: auto !important;
  box-shadow: 
    0 25px 80px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(161, 194, 189, 0.3),
    0 0 40px rgba(161, 194, 189, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1),
    inset 0 -1px 0 rgba(0, 0, 0, 0.1) !important;
  border: 1px solid rgba(161, 194, 189, 0.3) !important;
  z-index: 10001 !important;
  visibility: visible !important;
  opacity: 1 !important;
  display: block !important;
  animation: modalEntrance 0.5s cubic-bezier(0.16, 1, 0.3, 1), modalGlow 3s ease-in-out infinite;
  transform-origin: center;
}

@keyframes modalEntrance {
  0% {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes modalGlow {
  0%, 100% {
    box-shadow: 
      0 25px 80px rgba(0, 0, 0, 0.5),
      0 0 0 1px rgba(161, 194, 189, 0.3),
      0 0 45px rgba(161, 194, 189, 0.12),
      inset 0 1px 0 rgba(255, 255, 255, 0.1),
      inset 0 -1px 0 rgba(0, 0, 0, 0.1);
    border-color: rgba(161, 194, 189, 0.3);
    background: rgba(25, 24, 59, 0.75);
  }
  50% {
    box-shadow: 
      0 25px 80px rgba(0, 0, 0, 0.5),
      0 0 0 1px rgba(161, 194, 189, 0.4),
      0 0 50px rgba(161, 194, 189, 0.18),
      inset 0 1px 0 rgba(255, 255, 255, 0.12),
      inset 0 -1px 0 rgba(0, 0, 0, 0.1);
    border-color: rgba(161, 194, 189, 0.4);
    background: rgba(25, 24, 59, 0.8);
  }
}

.project-request-modal-close-btn {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(161, 194, 189, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(161, 194, 189, 0.3);
  color: #E7F2EF;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s var(--ease-out-expo);
  z-index: 10;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.project-request-modal-close-btn:focus {
  outline: 2px solid #A1C2BD;
  outline-offset: 2px;
}

.project-request-modal-close-btn:hover {
  background: rgba(161, 194, 189, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-color: rgba(161, 194, 189, 0.5);
  transform: rotate(90deg) scale(1.05);
  box-shadow: 0 4px 12px rgba(161, 194, 189, 0.25);
}

.project-request-modal-content input::placeholder,
.project-request-modal-content textarea::placeholder {
  color: rgba(161, 194, 189, 0.6) !important;
  opacity: 1 !important;
}

.project-request-modal-content input:focus,
.project-request-modal-content textarea:focus,
.project-request-modal-content select:focus {
  outline: none !important;
  border-color: #A1C2BD !important;
  background: rgba(231, 242, 239, 0.1) !important;
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  box-shadow: 
    0 0 0 3px rgba(161, 194, 189, 0.2),
    0 4px 12px rgba(161, 194, 189, 0.12) !important;
  transform: translateY(-1px);
}

.project-request-modal-content select option {
  background: #19183B !important;
  color: #E7F2EF !important;
  padding: 0.5rem !important;
}

.project-request-modal-content input:hover,
.project-request-modal-content textarea:hover,
.project-request-modal-content select:hover {
  border-color: rgba(161, 194, 189, 0.5) !important;
}

.project-request-modal-content .option-button:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 16px rgba(161, 194, 189, 0.25) !important;
  border-color: rgba(161, 194, 189, 0.6) !important;
}

.project-request-modal-close-btn:hover {
  background: var(--color-dark-20);
  transform: rotate(90deg);
}

.project-request-header {
  padding: 2.5rem 2.5rem 1.5rem;
  border-bottom: 1px solid rgba(161, 194, 189, 0.2);
  background: linear-gradient(180deg, rgba(161, 194, 189, 0.05) 0%, transparent 100%);
  position: relative;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.project-request-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(161, 194, 189, 0.3) 50%, 
    transparent 100%);
}

.project-request-header h2 {
  font-size: clamp(1.5rem, 3vw, 2rem);
  font-weight: 700;
  color: #E7F2EF;
  margin-bottom: 0.5rem;
  font-family: var(--font-sans);
  text-shadow: 0 2px 10px rgba(231, 242, 239, 0.15);
  letter-spacing: -0.02em;
}

.project-request-header p {
  font-size: 1rem;
  color: var(--text-on-light);
  opacity: 0.8;
  margin-bottom: 2rem;
}

.project-request-progress-bar {
  margin-top: 1.5rem;
}

.progress-steps-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.progress-step-circle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: var(--bg-secondary);
  border: 2px solid var(--color-light-10);
  color: var(--text-on-light);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 0.875rem;
  transition: all 0.3s var(--ease-out-expo);
  flex-shrink: 0;
}

.progress-step-circle.active {
  background: var(--color-dark);
  border-color: var(--color-dark);
  color: var(--color-light);
  box-shadow: 0 0 20px rgba(180, 232, 201, 0.3);
}

.progress-step-circle.completed {
  background: var(--color-dark);
  border-color: var(--color-dark);
  color: var(--color-light);
}

.progress-step-line {
  flex: 1;
  height: 2px;
  background: var(--color-light-10);
  transition: background 0.3s var(--ease-out-expo);
}

.progress-step-line.active {
  background: var(--color-dark);
}

.progress-text {
  font-size: 0.875rem;
  color: var(--text-on-light);
  opacity: 0.7;
  font-weight: 500;
  text-align: center;
}

.project-request-form-container {
  padding: 2rem 2.5rem 2.5rem;
  background: transparent;
}

.form-step-content {
  min-height: 300px;
}

.form-step-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-on-light);
  margin-bottom: 0.5rem;
  font-family: var(--font-sans);
}

.step-description {
  font-size: 0.9375rem;
  color: var(--text-on-light);
  opacity: 0.7;
  margin-bottom: 2rem;
}

.form-row-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-on-light);
  margin-bottom: 0.5rem;
  font-family: var(--font-sans);
}

.required-star {
  color: #ff6b6b;
}

.char-counter {
  font-weight: 400;
  opacity: 0.6;
  margin-left: 0.5rem;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 0.875rem 1.25rem;
  background: rgba(231, 242, 239, 0.06);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(161, 194, 189, 0.3);
  border-radius: var(--radius-md);
  color: var(--text-on-light);
  font-size: 1rem;
  font-family: var(--font-sans);
  transition: all 0.3s var(--ease-out-expo);
  box-sizing: border-box;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--color-dark);
  box-shadow: 0 0 0 3px rgba(180, 232, 201, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.form-group textarea::placeholder {
  color: var(--text-on-light);
  opacity: 0.5;
}

.field-help {
  display: block;
  font-size: 0.8125rem;
  color: var(--text-on-light);
  opacity: 0.6;
  margin-top: 0.5rem;
}

.option-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.option-button {
  padding: 1rem 1.25rem;
  background: rgba(231, 242, 239, 0.06);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 2px solid rgba(161, 194, 189, 0.3);
  border-radius: var(--radius-md);
  color: var(--text-on-light);
  font-size: 0.9375rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s var(--ease-out-expo);
  font-family: var(--font-sans);
  text-align: center;
}

.option-button:hover {
  border-color: var(--color-dark-30);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(5, 10, 31, 0.1);
}

.option-button.selected {
  background: rgba(161, 194, 189, 0.25);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-color: #A1C2BD;
  color: #E7F2EF;
  box-shadow: 
    0 4px 16px rgba(161, 194, 189, 0.3),
    0 0 20px rgba(161, 194, 189, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

.form-navigation-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 2.5rem;
  padding-top: 2rem;
  border-top: 1px solid var(--color-light-10);
}

.btn-nav {
  padding: 0.875rem 2rem;
  border-radius: var(--radius-md);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s var(--ease-out-expo);
  font-family: var(--font-sans);
  border: none;
}

.btn-nav-back {
  background: transparent;
  color: var(--text-on-light);
  border: 2px solid var(--color-light-10);
}

.btn-nav-back:hover {
  background: var(--bg-secondary);
  border-color: var(--color-dark-30);
}

.btn-nav-next,
.btn-nav-submit {
  background: #A1C2BD;
  color: #19183B;
  box-shadow: 0 4px 12px rgba(161, 194, 189, 0.35);
  font-weight: 600;
}

.btn-nav-next:hover,
.btn-nav-submit:hover {
  background: #8AEA92;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(138, 234, 146, 0.45);
}

.btn-nav-next:focus,
.btn-nav-submit:focus {
  outline: 2px solid #A1C2BD;
  outline-offset: 2px;
}

.btn-nav:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .project-request-modal-wrapper {
    padding: 0.5rem;
    align-items: flex-end !important;
  }

  .project-request-modal-content {
    max-height: 95vh !important;
    border-radius: 20px 20px 0 0 !important;
    width: 100% !important;
    max-width: 100% !important;
    animation: slideUpModal 0.4s cubic-bezier(0.16, 1, 0.3, 1);
  }

  @keyframes slideUpModal {
    from {
      transform: translateY(100%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  .project-request-header,
  .project-request-form-container {
    padding: 1.75rem 1.5rem;
  }

  .project-request-header {
    padding-top: 2rem;
  }

  .form-row-2 {
    grid-template-columns: 1fr;
    gap: 1.25rem;
    margin-bottom: 1.75rem;
  }

  .form-group {
    margin-bottom: 1.75rem;
  }

  .form-group input,
  .form-group textarea,
  .form-group select {
    padding: 1.125rem 1.5rem !important;
    font-size: 16px !important; /* Prevents zoom on iOS */
    min-height: 52px;
  }

  .form-group textarea {
    min-height: 140px !important;
    padding: 1.125rem 1.5rem !important;
  }

  .form-group label {
    margin-bottom: 0.875rem;
    font-size: 0.9375rem;
  }

  .option-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .option-button {
    padding: 1.125rem 1.25rem !important;
    min-height: 52px;
    font-size: 1rem !important;
  }

  .form-navigation-buttons {
    padding-top: 1.5rem;
    margin-top: 2rem;
    gap: 0.75rem;
  }

  .btn-nav {
    padding: 1.125rem 1.75rem !important;
    font-size: 1rem !important;
    min-height: 52px;
  }

  .project-request-modal-close-btn {
    top: 1.25rem;
    right: 1.25rem;
    width: 44px;
    height: 44px;
  }
}

/* Services CTA */
.services-cta {
  margin-top: 4rem;
  padding: 3rem 2rem;
  background: var(--bg-secondary);
  border-radius: var(--radius-xl);
  border: 1px solid var(--color-light-10);
  text-align: center;
}

.services-cta-content {
  max-width: 600px;
  margin: 0 auto;
}

.services-cta-title {
  font-size: clamp(1.5rem, 3vw, 2rem);
  font-weight: 700;
  color: var(--text-on-light);
  margin-bottom: 0.75rem;
  font-family: var(--font-sans);
}

.services-cta-text {
  font-size: 1.125rem;
  color: var(--text-on-light);
  opacity: 0.8;
  margin-bottom: 2rem;
}

/* Contact Quote CTA */
.contact-quote-cta {
