}

.contact-quote-content {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.contact-quote-icon {
  width: 56px;
  height: 56px;
  border-radius: var(--radius-md);
  background: var(--color-dark-10);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-dark);
  flex-shrink: 0;
}

.contact-quote-text {
  flex: 1;
  min-width: 200px;
}

.contact-quote-text h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-on-light);
  margin-bottom: 0.25rem;
  font-family: var(--font-sans);
}

.contact-quote-text p {
  font-size: 0.9375rem;
  color: var(--text-on-light);
  opacity: 0.7;
