@media (max-width: 768px) {
  .project-request-modal-container {
    width: 95%;
    max-height: 95vh;
  }

  .project-request-modal-header,
  .project-request-form {
    padding: 1.5rem;
  }

  .form-row {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .service-selector-grid,
  .budget-selector-grid,
  .timeline-selector-grid {
    grid-template-columns: 1fr;
  }

  .contact-quote-content {
    flex-direction: column;
    text-align: center;
  }

  .contact-quote-text {
    text-align: center;
  }

  .services-cta {
    padding: 2rem 1.5rem;
  }
