/* ============================================
   FLOATING CTA BUTTON
   ============================================ */
.floating-cta {
  position: fixed;
  z-index: 9999;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  background: var(--color-dark);
  color: var(--color-light);
  border: none;
  border-radius: var(--radius-full);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 
    0 8px 24px rgba(5, 10, 31, 0.3),
    0 0 30px rgba(180, 232, 201, 0.2);
  transition: all 0.3s var(--ease-out-expo);
  font-family: var(--font-sans);
  overflow: hidden;
}

/* Floating Quote Button - Left Side */
.floating-quote-button {
  bottom: 2rem;
  left: 2rem;
}

.floating-cta:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 
    0 12px 32px rgba(5, 10, 31, 0.4),
    0 0 40px rgba(180, 232, 201, 0.3);
}

.floating-cta:active {
  transform: translateY(-2px) scale(1.02);
}

.floating-cta-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.floating-cta-text {
  white-space: nowrap;
}

.floating-cta-pulse {
  position: absolute;
  inset: 0;
  border-radius: var(--radius-full);
  background: rgba(180, 232, 201, 0.3);
  animation: pulse 2s ease-in-out infinite;
  pointer-events: none;
}

@keyframes pulse {
  0%, 100% {
    opacity: 0;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}

@media (max-width: 768px) {
  .floating-quote-button {
    bottom: calc(5rem + env(safe-area-inset-bottom));
    left: 1.5rem;
    padding: 0.875rem 1.25rem;
    font-size: 0.9375rem;
  }

  .floating-cta-text {
    display: none;
  }

  .floating-cta-icon {
    width: 28px;
    height: 28px;
  }
}

/* ============================================
   AOS ANIMATION ENHANCEMENTS
   ============================================ */
[data-aos] {
  pointer-events: none;
}

[data-aos].aos-animate {
  pointer-events: auto;
}

/* Prevent layout shift and glitches for case-studies section */
#case-studies[data-aos] {
  min-height: 1px;
  will-change: opacity, transform;
  backface-visibility: hidden;
  transform: translateZ(0);
}

#case-studies[data-aos].aos-animate {
  will-change: auto;
}

/* Blog section: no upper padding (same as hero) */
#blog {
  padding-top: 0;
}

/* Force visibility for blog cards after component loads (AOS) */
#blog .blog-grid [data-aos] {
  min-height: 1px;
}

#blog .blog-grid [data-aos]:not(.aos-animate) {
  animation: forceVisible 0.1s 2s forwards;
}

@keyframes forceVisible {
  to {
    opacity: 1 !important;
    visibility: visible !important;
    transform: none !important;
  }
}

