/* Smooth scroll behavior */
html {
  scroll-behavior: smooth;
}

@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
  
  [data-aos] {
    opacity: 1 !important;
    transform: none !important;
  }
}

/* GPU acceleration for animations */
.testimonial-card,
.service-item-minimal,
.portfolio-item-modern,
.trusted-card-inner {
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/* ============================================
   UTILITIES
   ============================================ */
::selection {
  background: var(--color-light-30);
  color: var(--color-dark);
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: var(--bg-primary);
}

::-webkit-scrollbar-thumb {
  background: var(--color-light);
  border-radius: var(--radius-full);
  border: 3px solid var(--bg-primary);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-light-80);
}

/* ============================================
   SCROLL TO TOP BUTTON WITH PROGRESS
   ============================================ */
.scroll-to-top {
  position: fixed;
  bottom: 2rem;
  left: 2rem;
  width: 64px;
  height: 64px;
  background: rgba(5, 10, 31, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 2px solid var(--color-light-30);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 9998;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px) scale(0.9);
  transition: all 0.4s var(--ease-out-expo);
  color: var(--color-light);
  box-shadow: 
    0 4px 20px rgba(8, 18, 44, 0.4),
    0 0 20px rgba(185, 234, 209, 0.2);
}

.scroll-to-top.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) scale(1);
}

.scroll-to-top:hover {
  background: var(--color-light);
  border-color: var(--color-light);
  color: var(--color-dark);
  transform: translateY(-4px) scale(1.05);
  box-shadow: 
    0 8px 30px rgba(185, 234, 209, 0.4),
    0 0 40px rgba(185, 234, 209, 0.3),
    0 0 60px rgba(185, 234, 209, 0.2);
}

.scroll-to-top:active {
  transform: translateY(-2px) scale(1.02);
}

.scroll-to-top:focus-visible {
  outline: 3px solid var(--color-light);
  outline-offset: 2px;
}

.scroll-progress-circle {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.progress-ring {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  transform: rotate(-90deg);
  transition: all 0.3s var(--ease-out-expo);
}

.progress-ring-circle {
  stroke: var(--color-light);
  stroke-dasharray: 163.36;
  stroke-dashoffset: 163.36;
  transition: stroke-dashoffset 0.3s var(--ease-out-expo);
  stroke-linecap: round;
}

.scroll-to-top:hover .progress-ring-circle {
  stroke: var(--color-dark);
}

.scroll-percentage {
  position: relative;
  font-size: 0.625rem;
  font-weight: 700;
  color: var(--color-light);
  transition: color 0.3s var(--ease-out-expo);
  z-index: 1;
  font-family: var(--font-sans);
  line-height: 1;
}

.scroll-to-top:hover .scroll-percentage {
  color: var(--color-dark);
}

.scroll-arrow {
  position: absolute;
  bottom: 8px;
  width: 18px;
  height: 18px;
  color: var(--color-light);
  transition: all 0.3s var(--ease-out-expo);
  z-index: 2;
}

.scroll-to-top:hover .scroll-arrow {
  color: var(--color-dark);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .scroll-to-top {
    bottom: calc(5rem + env(safe-area-inset-bottom)) !important;
    left: 1.5rem !important;
    width: 56px !important;
    height: 56px !important;
    z-index: 10000 !important;
    display: flex !important;
  }
  
  .scroll-to-top.visible {
    opacity: 1 !important;
    visibility: visible !important;
    transform: translateY(0) scale(1) !important;
  }

  .progress-ring {
    width: 56px;
    height: 56px;
  }

  .progress-ring-circle {
    stroke-dasharray: 144.51;
    stroke-dashoffset: 144.51;
  }

  .scroll-percentage {
    font-size: 0.5625rem;
  }

  .scroll-arrow {
    width: 16px;
    height: 16px;
    bottom: 6px;
  }
}

/* ============================================
   BOTTOM NAVIGATION MENU
   ============================================ */
.bottom-nav {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background: rgba(5, 10, 31, 0.98);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-top: 1px solid var(--color-light-20);
  z-index: 9999;
  align-items: center;
  justify-content: space-around;
  gap: 0.5rem;
  padding: 0.75rem 0.5rem;
  padding-bottom: calc(0.75rem + env(safe-area-inset-bottom));
  box-shadow: 0 -4px 20px rgba(8, 18, 44, 0.3);
}

.bottom-nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  flex: 1;
  padding: 0.5rem;
  text-decoration: none;
  color: var(--color-light-50);
  transition: all 0.3s var(--ease-out-expo);
  border-radius: var(--radius-sm);
  position: relative;
  min-width: 0;
}

.bottom-nav-item:hover,
.bottom-nav-item:focus {
  color: var(--color-light);
  background: var(--color-light-10);
  transform: translateY(-2px);
}

.bottom-nav-item.active {
  color: var(--color-light);
}

.bottom-nav-item.active::before {
  content: '';
  position: absolute;
  top: -0.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 4px;
  background: var(--color-light);
  border-radius: 50%;
}

.bottom-nav-icon {
  width: 24px;
  height: 24px;
  stroke-width: 2;
  transition: all 0.3s var(--ease-out-expo);
}

.bottom-nav-item.active .bottom-nav-icon {
  transform: scale(1.1);
  stroke-width: 2.5;
}

.bottom-nav-number {
  font-size: 0.85em;
  font-weight: 600;
  opacity: 0.7;
  margin-right: 0.2em;
}

.bottom-nav-label {
  font-size: 0.625rem;
  font-weight: 500;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  transition: all 0.3s var(--ease-out-expo);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.bottom-nav-item.active .bottom-nav-label {
  font-weight: 600;
}

/* Desktop: Hide bottom nav */
@media (min-width: 769px) {
  .bottom-nav {
    display: none;
  }
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .footer-modern {
    padding-bottom: calc(4rem + env(safe-area-inset-bottom));
  }
  
  body {
    padding-bottom: calc(4.5rem + env(safe-area-inset-bottom));
  }
}

