/* ============================================
   LIGHTBOX GALLERY
   ============================================ */
.lightbox-gallery {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10001;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s var(--ease-out-expo);
}

.lightbox-gallery[aria-modal="true"] {
  opacity: 1;
  visibility: visible;
}

.lightbox-overlay {
  position: absolute;
  inset: 0;
  background: rgba(5, 10, 31, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.lightbox-close {
  position: absolute;
  top: 2rem;
  right: 2rem;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: rgba(180, 232, 201, 0.1);
  border: 2px solid rgba(180, 232, 201, 0.3);
  color: var(--color-light);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  transition: all 0.3s var(--ease-out-expo);
}

.lightbox-close:hover {
  background: rgba(180, 232, 201, 0.2);
  border-color: var(--color-light);
  transform: rotate(90deg);
}

.lightbox-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: rgba(180, 232, 201, 0.1);
  border: 2px solid rgba(180, 232, 201, 0.3);
  color: var(--color-light);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  transition: all 0.3s var(--ease-out-expo);
}

.lightbox-prev {
  left: 2rem;
}

.lightbox-next {
  right: 2rem;
}

.lightbox-nav:hover {
  background: rgba(180, 232, 201, 0.2);
  border-color: var(--color-light);
  transform: translateY(-50%) scale(1.1);
}

.lightbox-content {
  position: relative;
  width: 90%;
  max-width: 1200px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  z-index: 5;
}

.lightbox-image-wrapper {
  width: 100%;
  max-height: 75vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-lg);
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.lightbox-image {
  width: 100%;
  height: auto;
  max-height: 75vh;
  object-fit: contain;
  display: block;
}

.lightbox-counter {
  color: var(--color-light);
  font-size: 1rem;
  font-weight: 600;
  font-family: var(--font-sans);
}

.lightbox-thumbnails {
  display: flex;
  gap: 0.75rem;
  padding: 1rem;
  overflow-x: auto;
  max-width: 100%;
}

.lightbox-thumb {
  width: 80px;
  height: 80px;
  border-radius: var(--radius-md);
  overflow: hidden;
  border: 2px solid transparent;
  background: rgba(180, 232, 201, 0.1);
  cursor: pointer;
  transition: all 0.3s var(--ease-out-expo);
  flex-shrink: 0;
}

.lightbox-thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.lightbox-thumb.active {
  border-color: var(--color-light);
  box-shadow: 0 0 20px rgba(180, 232, 201, 0.4);
}

.lightbox-thumb:hover {
  transform: scale(1.1);
  border-color: rgba(180, 232, 201, 0.5);
}


